"use client"

import { useParams } from "next/navigation"
import Link from "next/link"
import { ArrowLeft } from "lucide-react"
import Header from "@/components/header"
import YoutubePlayer from "@/components/youtube-player"
import { Button } from "@/components/ui/button"
import { courseDetailsMap } from "@/lib/courses-data"

export default function PlaylistPage() {
    const params = useParams()
    const courseId = params.id as string
    const course = courseDetailsMap[courseId]

    if (!course) {
        return (
            <div className="min-h-screen bg-background">
                <Header />
                <main className="max-w-7xl mx-auto px-4 md:px-6 py-12">
                    <div className="text-center space-y-4">
                        <p className="text-lg text-muted-foreground">Course not found</p>
                        <Link href="/">
                            <Button variant="outline">
                                <ArrowLeft className="w-4 h-4 mr-2" />
                                Back to courses
                            </Button>
                        </Link>
                    </div>
                </main>
            </div>
        )
    }

    return (
        <div className="min-h-screen bg-background">
            <Header />
            <main className="max-w-7xl mx-auto px-4 md:px-6 py-8">
                <div className="mb-6 flex items-center gap-4">
                    <Link href={`/course/${courseId}`}>
                        <Button variant="outline" size="sm" className="bg-transparent">
                            <ArrowLeft className="w-4 h-4 mr-2" />
                            Back to Course
                        </Button>
                    </Link>
                    <h1 className="text-2xl font-bold truncate">{course.title} - Playlist</h1>
                </div>

                <div className="max-w-4xl mx-auto">
                    <YoutubePlayer
                        playlistId={course.youtubePlaylistId || course.url}
                        title={`${course.title} Playlist`}
                    />

                    <div className="mt-8 p-6 border rounded-lg bg-card text-card-foreground shadow-sm">
                        <h2 className="text-xl font-semibold mb-4">About this Playlist</h2>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                            <div>
                                <span className="text-muted-foreground">Instructor:</span>
                                <p className="font-medium">{course.instructor}</p>
                            </div>
                            <div>
                                <span className="text-muted-foreground">Category:</span>
                                <p className="font-medium">{course.category}</p>
                            </div>
                            <div>
                                <span className="text-muted-foreground">Language:</span>
                                <p className="font-medium">{course.language}</p>
                            </div>
                            <div>
                                <span className="text-muted-foreground">Students:</span>
                                <p className="font-medium">{course.students.toLocaleString()}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    )
}
